<template>
  <div>
    <div class="page-header">
      <h4 v-html="$t('modules.page.components.Content.'+page+'.title')"></h4>
    </div>
    <div class="divbj" v-html="$t('modules.page.components.Content.'+page+'.bj')"></div>
  </div>
</template>

<script>
export default {
  name: "Content",
  data() {
    var data = {
      page: ""
    };
    data.page = this.$route.name;

    document.title = "CCBOT - " + data.page;
    return data;
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      this.page = this.$route.name;
      document.title = "CCBOT - " + this.page;
    }
  }
};
</script>

